.App {
	background-color: aqua;
	height: 450px;
	width: 450px;
	border: solid;
	text-align: center;
}

.description {
	font-size: 20px;
}

.cipherInput {
	display: block;
	margin: auto;
	background-color: lightgray;
	border-radius: 10px;
	min-height: 30px;
	min-width: 220px;
	font-size: 25px;
	border: none;
	text-align: center;
}

.cipherOutput {
	display: block;
	margin: auto;
	border-radius: 5px;
	border: solid;
	font-size: 30px;
	width: 85%;
	max-height: 50%;
	word-wrap: break-word;
	margin-top: 20px;
}